body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(gray, black, gray);
}

.form-wrap {
  background-color: white;
  width: 400px;
  padding: 20px;
  border: 1px solid black;
  box-shadow: 5px 5px 10px black;
}

.form-wrap h2,
.form-wrap p {
  text-align: center;
}

.section {
  display: flex;
  flex-direction: column; /* stack labels above inputs */
  gap: 15px;
  margin-bottom: 30px;
}

.section label {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}

.section input[type="text"],
.section input[type="email"],
.section input[type="number"],
.section input[type="checkbox"],
.section select,
.section textarea {
  width: 100%;
  padding: 0.4rem;
  box-sizing: border-box;
  margin-top: 5px;
}

.submit-wrap {
  text-align: center;
}

.submit-btn {
  display: block;
  margin: 0 auto;
  padding: 0.4rem 0.5rem;
  border: 1px solid black;
}

.submit-btn:hover {
  cursor: pointer;
  background-color: #f3f3f3;
}

.submit-btn:disabled {
  cursor: not-allowed;
}

.submission-state {
  margin-top: 20px;
  padding: 10px;
  border-top: 1px solid #ccc;
}
